/// Overrides Jeet settings in ../vendor/jeet/_settings.scss

/// Jeet's default gutter value as a percentage.
/// @type Number
$jeet-gutter: 3 !default;

/// Jeet's variable for setting the calling order when using nested columns
/// sizes, prioritizing the parent instead of the child.
/// @type Bool
$jeet-parent-first: false !default;

/// Jeet's default layout direction.
/// @type String
$jeet-layout-direction: "LTR" !default;

/// Jeet's default max-width value.
/// @type Number|Unit
$jeet-max-width: 1280px !default;


// used for content
$inner-content-width: 800px;